<template>
    <div>

        <van-nav-bar title="专利数据库" left-text="返回" left-arrow @click-left="back">
            <van-icon name="search" slot="right"/>
        </van-nav-bar>

        <van-search placeholder="请输入要查询的专利号" v-model="value"/>

        <!--<van-cell-group v-for="(author,i) in authors" @click="method_1(i)">-->
        <!--<van-card :desc="author.desc" :title="author.name" thumb="docs/man_scientist_128px.png">-->
        <!--<div slot="price">{{author.a10.toFixed(2)}} 分</div>-->
        <!--<div slot="tags">-->
        <!--<van-tag plain type="danger" class="ttaagg" v-show="author.gyx">影响力人才</van-tag>-->
        <!--<van-tag plain type="primary" class="ttaagg" v-show="author.gcc">高产出人才</van-tag>-->
        <!--<van-tag plain type="success" class="ttaagg" v-show="author.gby">高被引人才</van-tag>-->
        <!--</div>-->
        <!--</van-card>-->
        <!--</van-cell-group>-->

        <van-cell-group v-for="(patent,i) in patents" @click="method_1(i)">
            <van-card :desc="patent.desc" :title="patent.TI" thumb="docs/doc_128px.png">
                <div slot="tags">
                    <van-tag plain type="primary" class="ttaagg" v-show="patent.gby">高被引专利</van-tag>
                </div>
            </van-card>
        </van-cell-group>

        <van-pagination
                v-model="currentPage"
                :page-count="totpage"
                mode="simple"
                @change="onSubmit"
        />
    </div>
</template>

<script>
    import {
        Button,
        CheckboxGroup,
        Card,
        SubmitBar,
        Toast,
        NavBar,
        Tag,
        Pagination,
        Search,
        Cell,
        CellGroup
    } from 'vant';
    import axios from 'axios';

    export default {
        components: {
            [Card.name]: Card,
            [Button.name]: Button,
            [SubmitBar.name]: SubmitBar,
            [CheckboxGroup.name]: CheckboxGroup,
            [NavBar.name]: NavBar,
            [Tag.name]: Tag,
            [Pagination.name]: Pagination,
            [Search.name]: Search,
            [Toast.name]: Toast,
            [Cell.name]: Cell,
            [CellGroup.name]: CellGroup,
        },

        data() {
            return {
                currentPage: 1,
                totpage: 1,
            };
        },

        computed: {},

        methods: {
            method_1(i) {
                // console.log(ev) // this is the event
                // console.log(i) // i is index of v-for
                this.$router.push('zhuanlidetail/' + this.patents[i].PN);
            },


            back() {
                this.$router.go(-1);
            },
            init() {
                this.onSubmit();
            },
            onSubmit() {
                const path = `api/zhuanli/` + this.currentPage
                axios.get(path)
                    .then(response => {
                            this.totpage = response.data.totpage
                            this.currentPage = response.data.curpage
                            this.patents = response.data.patents


                            // 话
                            for (var i = 0; i < this.patents.length; i++) {
                                this.patents[i]['desc'] = "专利号:" + this.patents[i]['PN'] + ','
                                    + "被引次数:" + this.patents[i]['CitedTimes']

                                if (this.patents[i]['CitedTimes'] >= 1) {
                                    this.patents[i]['gby'] = true
                                } else {
                                    this.patents[i]['gby'] = false
                                }

                            }

                            // console.log(response.data)
                            this.$forceUpdate()
                        }
                    )
                    .catch(error => {
                        console.log(error)
                    })

                this.$nexttick(() => {
                    window.scrollTo(0, 1)
                    window.scrollTo(0, 0)
                })
            }


        },
        created() {
            this.init()
        }
    }
    ;
</script>
<style lang="less">

    .ttaagg {
        margin-left: 5px;
    }
</style>